
/* global navigator */
import React, { useState } from 'react';
import PropTypes from 'prop-types';

import { ReactComponent as MarkerIcon } from 'assets/icons/Map_marker.svg';

import {
  Button,
} from 'reactstrap';

import './Marker.scss';

const Marker = ({ alarm }) => {
  const [isClicked, setIsClicked] = useState(false);
  const onClick = () => {
    setIsClicked(!isClicked);
  };

  const handleCopyClick = () => {
    navigator.clipboard.writeText(
      `
        –§–ò–û: ${alarm.User.firstName} ${alarm.User.lastName}
        –¢–ï–õ.: ${alarm.User.tel}
        –ê–¥—Ä–µ—Å: ${alarm.address}
        –®–∏—Ä–æ—Ç–∞: ${alarm.track[0][0]}
        –î–æ–ª–≥–æ—Ç–∞: ${alarm.track[0][1]}
      `,
    )
      .then(() => {
        onClick();
      })
      .catch(err => {
        console.log('Something went wrong', err);
      });
  };

  return (
    !isClicked
      ? (
      <div className="marker-container" onClick={onClick}>
        <MarkerIcon className="marker-icon"/>
      </div>
      )
      : (
        <div className='marker-opened'>
          <div className='d-flex align-items-top justify-content-between align-items-start'>
            <div className='alarm-info-container'>
              <div className={'alarm-info'}>
                {`–§–ò–û: ${alarm.User.firstName} ${alarm.User.lastName}`}
              </div>
              <div className={'alarm-info'}>
                {`–¢–ï–õ.: ${alarm.User.tel}`}
              </div>
              <div className={'alarm-info'}>
                {`–ê–¥—Ä–µ—Å: ${alarm.address}`}
              </div>
              <div className={'alarm-info'}>
                {`–®–∏—Ä–æ—Ç–∞: ${alarm.track[0][0]}`}
              </div>
              <div className={'alarm-info'}>
                {`–î–æ–ª–≥–æ—Ç–∞: ${alarm.track[0][1]}`}
              </div>
            </div>
            <button type="button" className="close" aria-label="Close" onClick={onClick}>
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <Button
            color="ghost-primary"
            onClick={handleCopyClick}
          >
            –°–∫–æ–ø–∏—Ä–æ–≤–∞—Ç—å
          </Button>
        </div>
      )
  );
};

Marker.propTypes = {
  alarm: PropTypes.object.isRequired,
};

export default Marker;
