import React from 'react';
import PropTypes from 'prop-types';
import ReactTable from 'react-table';
import { Badge } from 'reactstrap';
import { useStore } from 'effector-react';
import {
  operators,
  auth,
} from 'store';

import './ControlPanelAlarms.scss';

import { statusChecker } from 'helpers';

const Alarms = ({ alarms, alarmId, onClick }) => {
  const operatorsFromStore = useStore(operators);
  const authFromStore = useStore(auth);
  const columns = [
    {
      Header: '–°—Ç–∞—Ç—É—Å',
      accessor: 'status',
      Cell: row => {
        let status = 'danger';
        if (row.row.status === 10) {
          status = 'warning';
        }
        if (row.row.status === 20) {
          status = 'success';
        }

        return (
          <div className="status-container">
            <span className="status-text">{statusChecker(row.row.status)}</span>
            <Badge pill color={status}>!</Badge>
          </div>
        );
      },
    },
    {
      Header: '–í –æ–±—Ä–∞–±–æ—Ç–∫–µ –æ–ø–µ—Ä–∞—Ç–æ—Ä–æ–º',
      accessor: 'oid',
      Cell: row => {
        let operatorForRow = operatorsFromStore.find(id => id === row.row.oid);
        if (operatorForRow) {
          operatorForRow = `–í –æ–±—Ä–∞–±–æ—Ç–∫–µ: id${operatorForRow}`;
        }
        if (!operatorForRow && row.row.status) {
          operatorForRow = '–í –æ–±—Ä–∞–±–æ—Ç–∫–µ: –æ–ø–µ—Ä–∞—Ç–æ—Ä –Ω–µ –≤ —Å–µ—Ç–∏';
        }
        if (row.row.oid === authFromStore.user.id) {
          operatorForRow = '–í –æ–±—Ä–∞–±–æ—Ç–∫–µ –º–Ω–æ–π';
        }
        return (
          <div>
            <span>{operatorForRow || '–Ω–µ –≤–∑—è—Ç –≤ –æ–±—Ä–∞–±–æ—Ç–∫—É'}</span>
          </div>
        );
      },
    },
    {
      Header: '–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å',
      accessor: 'User',
      Cell: row => (
        <div>
          <span>{`${row.row.User.lastName} `}</span>
          <span>{row.row.User.firstName}</span>
        </div>
      ),
    },
    {
      Header: '–¢–µ–ª–µ—Ñ–æ–Ω',
      accessor: 'User.tel',
    },
    {
      Header: '–û–ø–ª–∞—á–µ–Ω',
      accessor: 'callIsPaid',
      Cell: row => {
        return (
          <div>
            <span>{`${row.row.callIsPaid ? '–î–∞' : '–ù–µ—Ç'} `}</span>
          </div>
        );
      },
    },
  ];

  const trProps = (state, rowInfo) => {
    const newProps = {
      onClick: (e, handleOriginal) => {
        onClick(rowInfo.original.id);
        if (handleOriginal) {
          handleOriginal();
        }
      },
      style: {
        textAlign: 'center',
        cursor: 'pointer',
        whiteSpace: 'unset',
      },
    };
    if (rowInfo.original.id === alarmId) {
      newProps.style = {
        ...newProps.style,
        background: '#20a8d8',
        color: 'white',
      };
    }
    return newProps;
  };

  return (
    <ReactTable
      data={alarms}
      columns={columns}
      showPagination={(alarms && alarms.length > 15)}
      showPaginationBottom={true}
      showPageSizeOptions={false}
      defaultPageSize={15}
      previousText='–ü—Ä–µ–¥—ã–¥—É—â–∞—è —Å—Ç—Ä–∞–Ω–∏—Ü–∞'
      nextText='–°–ª–µ–¥—É—é—â–∞—è —Å—Ç—Ä–∞–Ω–∏—Ü–∞'
      pageText= '–°—Ç—Ä–∞–Ω–∏—Ü–∞'
      ofText= '–∏–∑'
      rowsText= '—Å—Ç—Ä–æ–∫'
      minRows={0}
      style={{
        height: '80vh',
        borderLeft: '0px',
        borderTop: '0px',
        borderBottom: '0px',
      }}
      getTdProps={trProps}
    />
  );
};

Alarms.propTypes = {
  alarms: PropTypes.array.isRequired,
  alarmId: PropTypes.number.isRequired,
  onClick: PropTypes.func.isRequired,
};

export default Alarms;
